<template>
  <div class="about">
    <h1>{{id?"编辑":"新建"}}部门 </h1>
    <el-form label-width="120px" @submit.native.prevent="save">
        <el-form-item label="名称" >
            <el-input v-model="model.name"></el-input>
        </el-form-item>
        <el-form-item label="简介" >
            <el-input v-model="model.intro"></el-input>
        </el-form-item>
        <el-form-item label="图标" >
            <!-- action,上传地址，在main.js中
            getAuthHeaders()加请求头，在main.js中 -->
            <el-upload
            class="avatar-uploader"
            :action="uploadUrl"
            :headers="getAuthHeaders()"
            :show-file-list="false"
            :on-success="afterUpload">
            <img v-if="model.icon" :src="model.icon" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
    </el-form>
  </div>
</template>
<script> 
export default {
    props:{
        id:{}
    },
    data(){
        return {
            model:{},
        }
    },
    methods: {
        afterUpload(res){
            this.$set(this.model,'icon',res.url)
            console.log(res)
        },
        async save(){
            // let res
            if(this.id){
                await this.$http.put(`/rest/departments/${this.id}`,this.model)
            }else{
                await this.$http.post('/rest/departments',this.model);
            }
            this.$router.push('/departments/list')
            this.$message({
                type:"success",
                message:"success" 
            })
        },
        async fetch(){
            const res = await this.$http.get(`/rest/departments/${this.id}`)
            this.model = res.data
        },
        // async fetchParents(){
        //     const res = await this.$http.get(`/rest/items`)
        //     this.parents = res.data
        // }
    },
    created(){
        // this.fetchParents()
        this.id&&this.fetch()
    }
}
</script>
